import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MapWindowComponent } from './components/map-window/map-window.component';
import { MappingModuleConfiguration, MappingModuleConfigurationToken } from './mapping.module.configuration';

@NgModule({
  imports: [CommonModule],
  declarations: [
    MapWindowComponent
  ],
  exports: [
    MapWindowComponent
  ],
})
export class MappingModule {
  static forRoot(config: MappingModuleConfiguration): ModuleWithProviders<MappingModule> {
    return {
      ngModule: MappingModule,
      providers: [
        {
          provide: MappingModuleConfigurationToken,
          useValue: config
        }
      ]
    }
  }
}
