import { Component, OnInit, ChangeDetectionStrategy, Input, Inject } from '@angular/core';
import esriConfig from '@arcgis/core/config';
import WebMap from '@arcgis/core/WebMap';
import MapView from '@arcgis/core/views/MapView';
import { MappingModuleConfiguration, MappingModuleConfigurationToken } from '../../mapping.module.configuration';

@Component({
  selector: 'hwrm-mapping-map-window',
  templateUrl: './map-window.component.html',
  styleUrls: ['./map-window.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MapWindowComponent implements OnInit {

  constructor(@Inject(MappingModuleConfigurationToken) private config: MappingModuleConfiguration) {

  }

  ngOnInit(): void {

    // Consuming app can pass a local asset path otherwise assets will be
    // puled from CDN
    if (this.config.assetPath)
      esriConfig.assetsPath = this.config.assetPath;

    const webMap = new WebMap({
      basemap: "streets-vector"
    });

    const view = new MapView({
        container: 'map',
        map: webMap
    });

  }

}
